import React from 'react';
import { Jumbotron, Container, Col, Row, Card, CardBody, CardTitle, CardImg, CardText, Button, FormGroup, NavLink } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
const API_KEY = "a9f020ade0a6ba6d2ba84ba91345fb4e73fa64ad1c0b6221233b55c6f12c3b29";
const SEC_KEY = "a58a8b865b6c650eb94003187fae318d94b86e2c538e0c800670f0a7228c11cf";
const URL_KEY = "https://cors-anywhere.herokuapp.com/";

export class PickPhotos extends React.Component {
	state = {
		imgs : []
	}
	getPhotos = async (e) => {
		e.preventDefault();
		const keySearch = this.keySearch.value;
		const api_call = await fetch(`${URL_KEY}https://api.unsplash.com/search/collections?&query=${keySearch}&client_id=${API_KEY}`);

		const data = await api_call.json();
		console.log(data.results);
		this.setState({
			imgs: data.results
		})

		this.addForm.reset();
	}
	render() {
		return (
			<section className='pickphotos'>
				<Container>
					<Row className='mb-3'>
						<Col lg='8' className='mx-auto'>
							<div className='headingTitle'>
								<h1> <FontAwesomeIcon icon="camera" /> PickPhotos </h1>
							</div>
							<Card>
								<CardBody>
								<form onSubmit={(e) => (this.getPhotos(e))} className='form' ref={(input) => (this.addForm) = input} >
									<Row form>
										<Col md='10'>
											<FormGroup>
											<input type='teks' name='category' className='form-control' ref={(input) => (this.keySearch) = input} placeholder="Search Free Photos..." />
											</FormGroup>
										</Col>
										<Col md='2'>
											<FormGroup>
											<Button block> <FontAwesomeIcon icon="search" /> </Button>
											</FormGroup>
										</Col>
									</Row>
								</form>
								</CardBody>
							</Card>
						</Col>
					</Row>
					<Row className='mb-3'>
						{ this.state.imgs.map((img) => {
							return(
							<Col md='3' key={img.id}>
								<Card className='mb-3'>
									<CardImg top src={img.preview_photos[0].urls.full} />
									<CardBody>
										<CardTitle> Photo by : {img.user.first_name} </CardTitle>
										<NavLink href={img.preview_photos[0].urls.full} target="__blank"> 
											<FontAwesomeIcon icon="download" /> Download 
										</NavLink>
									</CardBody>
								</Card>
							</Col>
						);
						})}
					</Row>
					<div className='PoweredBy'>
						<h1> &copy; Powered by </h1> <NavLink href="https://unsplash.com/" target="__blank"> unsplash.com </NavLink>
					</div>
				</Container>
			</section>
		);
	}
}