import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';
import Anchorlink from 'react-anchor-link-smooth-scroll';

export class SidebarMainPage extends React.Component {
	handleClick = (e) => {
		const SidebarMainPage = document.getElementsByClassName('SidebarMainPage')[0];
			  SidebarMainPage.classList.toggle('SidebarMainPageToggle');
			  console.log(SidebarMainPage);
	}
	render() {
		return(
			<div className='SidebarMainPage'>
				<div className='CloseSidebar'>
					<i onClick={(e) => (this.handleClick(e))}> <FontAwesomeIcon icon="times" /></i>
				</div>
				<div className='MenuSidebar'>
					<ul>
						<li> <Anchorlink href='#page-1-mainpage'> Home </Anchorlink> </li>
						<li> <Anchorlink href='#page-2-mainpage'> Portofolio </Anchorlink> </li>
						<li> <Anchorlink href='#page-3-mainpage'> Contact </Anchorlink> </li>
					</ul>
				</div>
			</div>
		);
	}
}
