import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export class LeftbarWillyWonka extends Component{
	handleClick = (e) => {
		const CLeftbar = document.getElementsByClassName('LeftBar')[0];
			  CLeftbar.classList.toggle('LeftBarToggle');
	}
	render(){
		return(
			<section className='LeftbarWillyWonka'>
				<div className="LeftBar">
					<i onClick={(e) => this.handleClick(e)}>
						<FontAwesomeIcon icon="arrow-left" />
					</i>
					<div className="menuLeftbar">
						<NavLink to="/"> Home </NavLink>
						<NavLink to="/"> Message </NavLink>
						<NavLink to="/"> Gallery </NavLink>
						<NavLink to="/"> Social </NavLink>
					</div>
				</div>
			</section>
		);
	}
}
