import React from 'react';
import {MainPage} from './Components/MainPage/MainPage';
import {WeatherFinder} from './Components/WeatherFinder/WeatherFinder';
import {WillyWonka} from './Components/WillyWonka/WillyWonka';
import {ECommerce} from './Components/ECommerce/ECommerce';
import {PickPhotos} from './Components/PickPhotos/PickPhotos';

import { BrowserRouter, Switch, Route } from 'react-router-dom';
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {  faTimes,faCamera, faSearch, faDownload, faCloud, faCircle, faEnvelope, faPhone, faMobile, faTable, faHome, faTimesCircle, faCircleNotch, faArrowAltCircleUp, faBell, faUserGraduate, faUserTie, faUsers, faCreditCard, faTasks, faHandHoldingUsd, faMoneyCheckAlt, faArrowLeft, faArrowRight, faBars, faSignInAlt, faTachometerAlt, faBoxOpen, faThLarge, faFileAlt, faStroopwafel, faCalendarAlt, faAngleLeft } from '@fortawesome/free-solid-svg-icons';

library.add( faTimes,faCamera, faSearch, faDownload, faCloud, faCircle, faEnvelope, faPhone, faMobile, faTable, faHome, faTimesCircle, faCircleNotch, faArrowAltCircleUp, faBell, faUserGraduate, faUserTie, faUsers, faCreditCard, faTasks, faHandHoldingUsd, faMoneyCheckAlt, faArrowLeft, faArrowRight, faBars, faSignInAlt, faTachometerAlt, faBoxOpen, faThLarge, faFileAlt, faStroopwafel, faCalendarAlt, faAngleLeft )

export class Router extends React.Component {
	render() {
		return(
			<BrowserRouter>
				<Switch>
					<Route path='/' component={MainPage} exact />
					<Route path='/weatherfinder' component={WeatherFinder} />
					<Route path='/willywonka' component={WillyWonka} />
					<Route path='/ecommerce' component={ECommerce} />
					<Route path='/pickphotos' component={PickPhotos} />
				</Switch>
			</BrowserRouter>
		);
	}
}