class BtnOLeftBar extends React.Component{
	handleClick(){
		// const btnleftBar = document.getElementsByClassName('btnleftBar')[0];
		// 	  btnleftBar.style.color = 'red';

		const OLeftBar = document.getElementsByClassName('LeftBar')[0];
			  OLeftBar.classList.toggle('LeftBarToggle');
	}

	render(){
		return(
			<a href="#" onClick={(e) => this.handleClick(e)} className="BtnOLeftBar"> <FontAwesomeIcon icon="bars" /> </a>
		);
	}
}

class BtnORightBar extends React.Component{
	handleClick(){
		const RightbarWillyWonka = document.getElementsByClassName('RightBar')[0];
			  RightbarWillyWonka.classList.toggle('RightbarToggle');
	}
	render(){
		return(
			<a href="#" onClick={(e) => this.handleClick(e)} className="BtnORightBar"> <FontAwesomeIcon icon="sign-in-alt" /> </a>
		);
	}
}