import React from 'react';
import {RestWeather} from './RestWeather';
import {Container, Row, Col, Card, CardBody, Button, FormGroup, NavLink } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const API_KEY = "6cca5d9ad193e48a8fe2393bb7ce85d3";
const API_URL = "http://cors-anywhere.herokuapp.com/";
export class WeatherFinder extends React.Component {
	state = {
		city: undefined,
		country: undefined,
		temp: undefined,
		humidity: undefined,
		windspeed: undefined,
		rain: undefined,
		snow: undefined,
		description: undefined
	}
	getWeather = async (e) => {
		e.preventDefault();
		const cityName = this.cityName.value;
		const api_call = await fetch(`${API_URL}https://api.openweathermap.org/data/2.5/find?q=${cityName}&units=metric&appid=${API_KEY}`);
		const data = await api_call.json();
		console.log(data.list[0]);

		this.setState({
			city: data.list[0].name,
			country: data.list[0].sys.country,
			temp: data.list[0].main.temp,
			humidity: data.list[0].main.humidity,
			windspeed: data.list[0].wind.speed,
			rain: data.list[0].rain,
			snow: data.list[0].snow,
			description: data.list[0].weather[0].description
		})

		this.addForm.reset();
	}
	render() {
		return(
			<section className='WeatherFinder'>
				<div className='page-1-WeatherFinder'>
					<Container>
						<Row>
							<Col lg='8' className='mx-auto'>
								<div className='text-center'>
									<div className="headingTitle">
										<NavLink href="https://openweathermap.org/" target="__blank"> <FontAwesomeIcon icon="cloud" /> </NavLink>
										<h1>  Weather Info </h1>
									</div>	
								</div>
								<Card>
									<CardBody>
										<form onSubmit={(e) => (this.getWeather(e))} className='form' ref={(input) => (this.addForm) = input}>
											<Row form>
												<Col md='10'>
													<FormGroup>
														<input type='teks' ref={(input) => (this.cityName) = input} className='form-control' placeholder="Type Your City..."/>
													</FormGroup>
												</Col>
												<Col md='2'>
													<FormGroup>
														<Button block> <FontAwesomeIcon icon="search" /> </Button>
													</FormGroup>
												</Col>
											</Row>										
										</form>
										<RestWeather 
										city={this.state.city} 
										country={this.state.country} 
										temp={this.state.temp}
										humidity={this.state.humidity}
										windspeed={this.state.windspeed}
										rain={this.state.rain}
										snow={this.state.snow}
										description={this.state.description} />
									</CardBody>
								</Card>
								<div className='PoweredBy'>
									<h1> &copy; Powered by</h1>
									<NavLink href="https://openweathermap.org/" target="__blank"> openweathermap.com </NavLink>
								</div>
							</Col>
						</Row>
					</Container>
				</div>
			</section>
		);
	}
}