import React, { Component } from 'react';

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Button } from 'reactstrap';

 import AnchorLink from 'react-anchor-link-smooth-scroll';

export class NavbarWillyWonka extends Component{
	constructor(props) {
	    super(props);

	    this.toggle = this.toggle.bind(this);
	    this.state = {
	      isOpen: false
	    };
	  }
	  toggle() {
	    this.setState({
	      isOpen: !this.state.isOpen
	    });
	  }
	render(){
		return(
			<section className="NavbarWillyWonka">
				<Navbar light expand="md" className="fixed-top">
		          <NavbarBrand href="/">Willy Wonka</NavbarBrand>
		          <NavbarToggler onClick={this.toggle} />
		          <Collapse isOpen={this.state.isOpen} navbar>
		            <Nav className="ml-auto" navbar>
		              <NavItem>
		              	<AnchorLink href="#serviceWillyWonka">Services</AnchorLink>
		              	<AnchorLink href="#featureWillyWonka">Feature</AnchorLink>
		              </NavItem>
		            </Nav>
		          </Collapse>
		        </Navbar>
			</section>
		);
	}
}