import React from 'react';
import { CardTitle, CardText } from 'reactstrap';
export const RestWeather = (props) => (
	<div className='RestWeather'>
		{ props.city && props.country && <p> Location : { props.city }, { props.country } </p>  }
		{ props.temp && <p> Temperature : { props.temp } </p>  }
		{ props.humidity && <p> Humidity : { props.humidity } </p>  }
		{ props.windspeed && <p> Windspeed : { props.windspeed } </p>  }
		{ props.rain && <p> Rain : { props.rain } </p>  }
		{ props.snow && <p> Snow : { props.snow } </p>  }
		{ props.description && <p> Descriptrion : { props.description } </p>  }
	</div>

);