import React from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  Button } from 'reactstrap';

import Anchorlink from 'react-anchor-link-smooth-scroll';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export class NavbarMainPage extends React.Component {
	constructor(props) {
	    super(props);
	    this.state = {
	      navbarBackgroundColor : 'rgba(0,0,0,0)'
	    };
	  }

	  NavbarScrollColor = e => {
	  	if(window.scrollY > 5){
	  		this.setState({navbarBackgroundColor: '#f1f2f6'})
	  	}else{
	  		this.setState({navbarBackgroundColor: 'rgba(0,0,0,0)'})
	  	}
	  }

	  componentDidMount(){
	  	window.addEventListener('scroll', this.NavbarScrollColor)
	  }

	  render() {
	    return (
	      <div className='NavbarMainPage'>
	        <Navbar light expand="md" className='fixed-top' style={{backgroundColor : this.state.navbarBackgroundColor}}>
	        <ButtonOpen />
	        </Navbar>
	      </div>
	    );
	  }
	}


class ButtonOpen extends React.Component {
	handleClick = (e) => {
		e.preventDefault();
		const SidebarMainPage = document.getElementsByClassName('SidebarMainPage')[0];
			  SidebarMainPage.classList.toggle('SidebarMainPageToggle');
			  console.log(SidebarMainPage);
	}
	render() {
		return(
			<NavbarBrand>
				 <i onClick={(e) => (this.handleClick(e))}> <FontAwesomeIcon icon="bars" /></i>
			</NavbarBrand>
		);
	}
}

